            console.log('2323232');
            console.log(L);

            var mymap = L.map("mapid", {
                zoomControl: false,
                attributionControl: false,
                zoom: 3,
                minZoom: 2,
                maxZoom: 5
            }).setView([10, 5], 3);

            var southWest = L.latLng(- 700, -200);
            var northEast = L.latLng(700, 200);
            var bounds = L.latLngBounds(southWest, northEast);
            mymap.setMaxBounds(bounds);

            var map_svg, attackBox;
            var attack = {};
            var marks;

            d3.json("./static/json/world_map.json",
                function (a, b) {
                    function removeAll() {
                        for (var a = f.length - 1; a >= 0; a--) {
                            f[a].remove();
                        }
                        f = [];
                    }
                    attack.run = function () {
                        for (var a = f.length - 1; a >= 0; a--) {
                            if (f[a].isEnd()) {
                                if (!f[a].isCleaning) {
                                    f[a].isCleaning = true;
                                    f[a].delete();
                                    f.splice(a, 1);
                                }
                            } else {
                                f[a].update();
                                f[a].render();
                            }
                        }
                    };
                    var c = topojson.feature(b, b.objects.countries);
                    L.geoJSON(c, {
                        style: {
                            color: "#4195d2",
                            opacity: .35,
                            weight: .7,
                            fillColor: "#1955c8",
                            fillOpacity: .5
                        }
                    }).addTo(mymap);
                    var d = d3.select("#mapid").select("svg");
                    map_svg = d;

                    marks = new Marks(mymap, map_svg);

