                            })
                            .transition().duration(500)
                            .attr("transform", function () {
                                var a = "translate(" + (_this.ep_px.x - 20.5) + "," + (_this.ep_px.y - 20.5) + ")scale(0.04)";
                                return a;
                            })
                            .style("opacity", "1"),
                            this.ico.transition().duration(1500).style("opacity", "0.0").attr("r", 0).remove();
                        this.group.append("circle")
                            .attr("cx", this.ep_circle.attr("cx")).attr("cy", this.ep_circle.attr("cy")).attr("r", 0).attr("fill", "rgba(0,0,0,0)")
                            .style("stroke-width", (this.type == 'APT' ? 4 : 2) * this.curZoom)
                            .style("stroke", this.planeColor)
                            .transition().duration((this.type == 'APT' ? 2000 : 1000)).ease(Math.sqrt).attr("r", (this.type == 'APT' ? 60 : 30) * this.curZoom).style("stroke-opacity", 1e-6).remove();
                        this.group.transition().delay(10000).style("opacity", "0.0").remove();
                        this.plane.remove();
                    },
                    this.remove = function () {
                        this.bp_circle.remove(),
                            this.road.remove(),
                            this.ep_circle.remove(),
                            this.group.remove()
                    },
                    this.d_plane = "M-175 240 L58 0 L65 0 L65 7 Z"
            };
            console.log(category_legend);

            for (var key in pathList) {
                var d = pathList[key];
                var li = category_legend.append("li");
                li.append("i").append("svg")
                    .attr("fill", colorList[key])
                    .attr("x", "0px")
                    .attr("y", "0px")
                    .attr("width", "200")
                    .attr("height", "200")
                    .attr("viewBox", "0 0 200 200")
                    .attr("enable-background", "new 0 0 200 200")
                    .attr("xml:space", "preserve")
                    .append("path")
                    .attr("d", d);
                li.append("span").text(categoryNames[key]);
            }
            console.log(li);
        },
        //雷达图开始
        setCategory_top(data) {
            var categoryArr = [];
            for (var i = data.length - 1; i >= 0; i--) {
                categoryArr.push(data[i][1]);
            }
            var chart_category = {
                backgroundColor: 'none',
                radar: {
                    indicator: [
                        { name: '钓鱼链接和网站' },
                        { name: '恶意程序' },
                        { name: '恶意IP' },
                        { name: '僵尸网络C&C' },
                        { name: '移动恶意程序' },
                        { name: '恶意域名' },
                        { name: 'APT攻击' }
                    ],
                    shape: 'circle',
                    splitNumber: 6,
                    name: {
                        textStyle: {
                            color: 'rgb(255, 255,255)',
                            fontSize: 0.7 + 'vw'
                        }
                    },
                    splitLine: {
                        lineStyle: {
                            color: [
                                'rgba(255, 255, 255, 0.1)', 'rgba(255, 255, 255, 0.2)',
                                'rgba(255, 255, 255, 0.3)', 'rgba(255, 255, 255, 0.3)',
                                'rgba(255, 255, 255, 0.4)', 'rgba(255, 255, 255, 0.4)',
                                'rgba(255, 255, 255, 0.5)'
                            ].reverse()
                        }
                    },
                    splitArea: {
                        show: false
                    },
                    axisLine: {
                        lineStyle: {
                            color: 'rgba(255, 255, 255, 0.4)'
                        }
                    }
                },
                series: [
                    {
                        type: 'radar',
                        lineStyle: {
                            normal: {
                                width: 1,
                                opacity: 0.2
                            }
                        },
                        data: [categoryArr],
                        symbol: 'none',
                        itemStyle: {
                            normal: {
                                color: '#d1e1f8'
                            }
                        },
                        areaStyle: {
                            normal: {
                                opacity: 0.3
                            }
                        }
                    }
                ]

            };
        },
        //受害区域图开始
        setDst_top() {
            var option = {
                backgroundColor: 'none',
                tooltip: {
                    trigger: 'item',
                    formatter: function (event) {
                        return 'Top ' + (event.dataIndex + 1) + '<br>' +
                            event.data.name + '：' + event.data.value[2];
                    }
                },
                geo: {
                    map: 'china',
                    label: {
                        emphasis: {
                            show: false
                        }
                    },
                    roam: false,
                    itemStyle: {
                        normal: {
                            areaColor: '#bad1f4',
                            borderColor: '#030917',
                            opacity: .5
                        },
                        emphasis: {
                            areaColor: '#2a333d'
                        }
                    }
                },
                series: [
                    {
                        type: 'effectScatter',
                        coordinateSystem: 'geo',
                        data: $scope.dst_top,
                        symbolSize: function (val) {
                            return 8 + Math.log(val[2]);
                        },
                        hoverAnimation: false,
                        itemStyle: {
                            normal: {
                                color: '#ea7900',
                                shadowBlur: 6,
                                shadowColor: '#333'
                            }
                        },
                        zlevel: 1
                    }
                ]
            }
        },
        init() {
            for (var i = 0; i < 7; i++) {
                this.attackList.push({ indicator: '　' });
            }
            console.log(this.attackList);

        },
        readCountry(List) {
            this.country_top = [];
            for (var i = 0; i < 7; i++) {
                var ss = List[0][1] - List[6][1];
                List[i][3] = Math.floor(30 + ((List[i][1] - List[6][1]) / ss) * 60) + "%";
                this.country_top.push(List[i]);
            }
            console.log(this.country_top);

        },
        pushToAttackList(atc) {
            this.attackList.push(atc);
            if (this.attackList.length > 7) {
                this.attackList.shift();
            }
            this.dst_count(atc.dst_subdivisions_name);
            setTimeout(function () {
                this.setDst_top();
            }, 0);
        },
        dst_init() {
            this.dst_top = [];
        },
        dst_count(name) {
            if (this.dst_top_all[name]) {
                this.dst_top_all[name].value[2]++;
            }
            var value = this.dst_top_all[name].value[2];
            var isTOP1 = true;
            for (var i = this.dst_top.length - 1; i >= 0; i--) {
                var item = this.dst_top[i];
                if (item.name == name) {
                    this.dst_top.splice(i, 1);
                    continue;
                }
                if (item.value[2] >= value) {
                    this.dst_top.splice(i + 1, 0, this.dst_top_all[name]);
                    isTOP1 = false;
                    break;
                }
            }
            if (isTOP1) {
                this.dst_top.splice(0, 0, this.dst_top_all[name]);
            }
            if (this.dst_top.length > 7) {
                this.dst_top.pop();
            }
        }
    },
    mounted() {
        console.log(this.attackList);
        this.init();
        this.d3map();
        console.log(this.attackList);

    }
}
</script>

<style scoped lang="less">
@import "../common/css/normalize.css";
@import "../common/css/leaflet.css";
@import "../common/css/flg.css";
@import "../common/css/plane.css";
.homemap {
  width: 100%;
  height: 100%;
  color: #000;
}
</style>



